import service from '../service.js'


export function login(data) {
    return service({
        method: 'post',
        url: '/login',
        data
    })
}

export function students(params) {
    return service({
        method: 'get',
        url: '/students',
        params
    })
}

export function studentDel(id) {
    return service({
        method: 'delete',
        url: `/students/${id}`
    })
}

export function getInfo() {
    return service({
        method: 'get',
        url: '/info'
    })
}

export function infoDel(id) {
    return service({
        method: 'delete',
        url: `/info/${id}`
    })
}

export function dataview() {
    return service({
        method: 'get',
        url: '/dataview'
    })
}

export function travel() {
    return service({
        method: 'get',
        url: '/travel'
    })
}